export default class LostBattleArray {
    public list: string[] = [];

    constructor()
    {
        let temp = localStorage.getItem('lostBattle');
        if(temp)
        {
            this.list = JSON.parse(temp);
        }
        else
        {
            this.list = [];
        }
    }

    private commitChanges()
    {
        localStorage.setItem('lostBattle', JSON.stringify(this.list));
    }

    private getDaysDifference(date1: string, date2: string): number
    {
        let msDiff = new Date(date1).valueOf() - (new Date(date2)).valueOf();
        msDiff = Math.abs(msDiff);
        return Math.floor(msDiff/(1000*60*60*24));
    }

    get daysSinceLastLostBattle(): number
    {
        let curDate: string = (new Date()).toDateString();
        let output: number = -1;

        if(this.list.length > 0)
        {
            output = this.getDaysDifference(curDate, this.list[0]);
            this.list.forEach((elem) => {
                let daysDiff = this.getDaysDifference(curDate, elem);
                if(daysDiff < output) { output = daysDiff; }
            });
        }

        return output;
    }

    isDateLostBattle(dateStr: string): boolean
    {
        return (this.list.indexOf(dateStr) != -1);
    }

    addLostBattle(dateStr: string)
    {
        if(!this.isDateLostBattle(dateStr))
        {
            this.list.push(dateStr);
            this.commitChanges();
        }
    }

    removeLostBattle(dateStr: string)
    {
        let index = this.list.indexOf(dateStr);
        if(index != -1)
        {
            this.list.splice(index, 1);
            this.commitChanges();
        }
    }
}