export default class ManPwrArray
{
    public dict: { [index: string]: string } = {};

    public constructor(dict: { [index: string]: string } = {})
    {
        let temp = localStorage.getItem('manpwr');
        if(temp)
        {
            this.dict = JSON.parse(temp);
        }
        else
        {
            this.dict = {};
        }
    }

    public get outputData()
    {
        return this.dict;
    }

    private getManPwrCountOnDate(date: string): number
    {
        return (this.dict[date] != undefined) ? this.dict[date].length : 0;
    }

    private filter()
    {
        let datesToFilter: string[] = [];
        for (var date in this.dict)
        {
            if(this.getManPwrCountOnDate(date) == 0)
            {
                datesToFilter.push(date);
            }
        }
        datesToFilter.forEach((date) => {delete this.dict[date]});
    }

    public get consecutiveManPwrDays(): number
    {
        let curDate: Date = new Date();
        let output: number = 0;
        if(this.getManPwrCountOnDate(curDate.toDateString()) == 6)
        {
            output++;
        }
        curDate.setDate(curDate.getDate() - 1);
        while(this.getManPwrCountOnDate(curDate.toDateString()) == 6)
        {
            output++;
            curDate.setDate(curDate.getDate() - 1);
        }
        return output;
    }

    public get manPwrDaysForWeek(): number
    {
        let curDate: Date = new Date();
        let output: number = 0;
        for(let i = 0; i < 7; i++)
        {
            curDate.setDate(curDate.getDate() - 1);
            output += this.getManPwrCountOnDate(curDate.toDateString());
        }
        return output;
    }

    public get partialDates(): string[]
    {
        let output: string[] = [];
        Object.keys(this.dict).forEach((key) => {
            if((this.dict[key].length > 0) && (this.dict[key].length < 6))
            {
                output.push(key);
            }
        });
        return output;
    }

    public get fullDates(): string[]
    {
        let output: string[] = [];
        Object.keys(this.dict).forEach((key) => {
            if((this.dict[key].length == 6))
            {
                output.push(key);
            }
        });
        return output;
    }

    public setManpwr(date: string, input: string)
    {
        this.dict[date] = input;
        this.filter();
    }

    public update(date: string)
    {
        this.filter();
        localStorage.setItem('manpwr', JSON.stringify(this.dict));
    }
}